.App {
  text-align: center;
}

header {display:flex;width:100%;background:#000;}
header h1 {color:#fff;}
div {box-sizing:border-box;}
.list {padding-left:20px;text-align:left;border-bottom:1px solid grey;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.modal {margin-top:20px;padding:20px;background:#eee;text-align:left;}
.box {width:200px;height:200px;margin:0 auto;margin-top:25px;background:red;border-radius:50%;}
h4 {display:inline-block;}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
